import { Injectable } from '@angular/core';
import { ContactoAgente, SesionVideoLLamada } from '../domain/cckall.domain';
import { StorageService } from './storage.service';

@Injectable({
  providedIn: 'root'
})
export class SessionService {

  private sesion: SesionVideoLLamada = new SesionVideoLLamada();
  private usuarioApp: ContactoAgente;

  constructor(
    private storageService: StorageService
  ) { }

  guardarUsuarioApp(contactoAgente: ContactoAgente): void {
    this.usuarioApp = contactoAgente;
    this.storageService.guardarValor('usuario', this.usuarioApp);
  }

  guardarVideollamadaId(videollamadaId: string): void {
    this.sesion.videollamadaId = videollamadaId;
    this.storageService.guardarValor('videollamadaId', videollamadaId);
  }

  guardarToken(token: string): void {
    this.sesion.token = token;
    this.storageService.guardarValor('token', token);
  }

  guardarEmisor(contacto: ContactoAgente): void {
    this.sesion.emisor = contacto;
  }

  guardarReceptor(contacto: ContactoAgente): void {
    this.sesion.receptor = contacto;
  }

  obtenerSesionVideoLLamada(): SesionVideoLLamada {
    return this.sesion;
  }

  obtenerUsuarioApp(): ContactoAgente {
    return this.usuarioApp;
  }

  limpiarSesionVideoLLamada(): void {
    this.sesion = new SesionVideoLLamada();
  }

}
