import { Component, OnDestroy, HostListener } from '@angular/core';
import { ContactoAgente } from './domain/cckall.domain';
import { ContactoAgenteService } from './service/contacto-agente.service';
import { Router } from '@angular/router';
import { forkJoin, Subscription } from 'rxjs';
import { map } from 'rxjs/operators';
import { WebsocketService } from './service/websocket.service';
import { MensajeWebsocket, TipoMensaje, MensajeVideoLLamada } from './domain/websocket.domain';
import { SessionService } from './service/session.service';
import { VideoLlamadaService } from './service/video-llamada.service';
import { NotificacionService } from './service/notificacion.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnDestroy {

  // --- propiedades para identificar usuario actual de app ---
  idAgente = 18362;
  conectado = false;
  usuarioApp: ContactoAgente;
  contactos: ContactoAgente[];
  // --- subscripcion de mensajes ws ---
  subscripcionMensajesWs: Subscription;
  // --- propiedades para notificacion de llamada saliente o entrante ---
  saliente = true;
  videoCallingVisible = false;
  nombreContacto: string;
  videollamada: MensajeVideoLLamada;

  constructor(
    private router: Router,
    private contactoAgenteService: ContactoAgenteService,
    private websocketService: WebsocketService,
    private sesionService: SessionService,
    private videollamadaService: VideoLlamadaService,
    private notificacionService: NotificacionService
  ) {}

  ngOnDestroy(): void {
    this.subscripcionMensajesWs.unsubscribe(); // --- cerrar conexion ws ---
  }

  iniciarSesion(idAgente: number) {
    console.log('Iniciar sesion con id ' + idAgente);
    this.idAgente = idAgente;
    // --- Multiples subscripciones ( funcion forkJoin() )a peticiones http y a observable de conexion establecida
    //     con websocket ---
    const sesionUsuario$ = forkJoin(
        this.contactoAgenteService.buscarContactoAgenteApp(idAgente),
        this.contactoAgenteService.buscarTodosContactoAgente(),
        this.websocketService.getConnection()
      ).pipe(
        map(([agente, contactos, conexionWs]) => {
            contactos = contactos.filter( c => c.id !== agente.id); // filtrar contactos que no sea el usario de la app
            return { agente, contactos, conexionWs }; // ---operador map() cambia devolucion de array a un objeto.
        })
      );
    const subscripcion$ = sesionUsuario$.subscribe(
     ({ agente , contactos, conexionWs }) => {
        console.log('Sesion usuario iniciada de ' + JSON.stringify(agente));
        console.log(conexionWs);
        this.usuarioApp = agente;
        this.conectado = true;
        this.contactos = contactos;
        this.sesionService.guardarUsuarioApp(this.usuarioApp);
        subscripcion$.unsubscribe(); // --- cerrar recursos usados ---
        // --- susbscribirse a mensjaes de websocket ---
        this.subscripcionMensajesWs = this.websocketService.getMensajes()
        .subscribe(
          (msg) => {
            this.processMensajeWebsocket(msg);
            console.log('se ha procesado el mensaje');
          },
          error => {
            console.log('Error', error);
          },
          () => {
            console.log('se ha cerrado la conexion de websocket');
            // this.router.navigate(['']);
          }
        );
        // --- enviar mensjae de registro de usuario ---
        const msgRegistro = {
            fecha: new Date(),
            tipoMensaje: TipoMensaje.REGISTRO_USUARIO,
            contenido: agente
        };
        this.websocketService.enviarMensajeWebsocket(msgRegistro);
      },
      error => {
        console.log('Error', error);
      }
    );
  }

  onSeleccionarContacto(contacto: ContactoAgente): void {
    console.log('contacto seleccionado: ' + JSON.stringify(contacto));
  }

  onVideollamadaContacto(contacto: ContactoAgente): void {
    if (this.conectado) {
      console.log('iniciar videollamada a contacto : ' + JSON.stringify(contacto));
      this.saliente = true;
      this.videoCallingVisible = true;
      this.nombreContacto = contacto.id + ' - ' + contacto.nombre + ' ' + contacto.apellido;
      const ms = new MensajeWebsocket<MensajeVideoLLamada>();
      ms.tipoMensaje = TipoMensaje.INICIAR_VIDEO_LLAMADA;
      const contenido = new MensajeVideoLLamada();
      contenido.emisor = this.usuarioApp;
      contenido.receptor = contacto;
      ms.contenido = contenido;
      console.log('enviando msg=' + JSON.stringify(ms));
      this.videollamada = contenido;
      this.websocketService.enviarMensajeWebsocket(ms);
    } else {
      console.log('Warning', 'No hay conexion establecida con servicio de videollamadas!!!');
    }
  }

  processMensajeWebsocket(mensaje: MensajeWebsocket<any>) {
    console.log('Procesar mensaje ' + JSON.stringify(mensaje));
    switch (mensaje.tipoMensaje) {
      // --- token openvidu para integrarse a una videollamada ---
      case TipoMensaje.TOKEN_VIDEOLLAMADA:
        console.log('Contenido recibido: ' + JSON.stringify(mensaje.contenido));
        this.sesionService.guardarToken(mensaje.contenido.token);
        this.sesionService.guardarVideollamadaId(mensaje.contenido.videollamadaId);
        this.videoCallingVisible = false;
        this.router.navigate(['/video', this.idAgente]);
      break;
      // --- Un contacto solicita una videollamada ---
      case TipoMensaje.SOLICITUD_VIDEO_LLAMADA:
        this.videoCallingVisible = true;
        this.saliente = false;
        this.nombreContacto = mensaje.contenido.emisor.id + ' - ' +
        mensaje.contenido.emisor.nombre + ' ' + mensaje.contenido.emisor.apellido;
        this.videollamada = mensaje.contenido;
      break;
      // --- se recibe el videollamdaId de una videollamada iniciada por este usuario.
      case TipoMensaje.VIDEOLLAMADA_ID_ASIGNADO:
        this.videollamada.videollamadaId = mensaje.contenido;
      break;
      // --- se recibe notificacion que se cancelo una llamada ---
      case TipoMensaje.CANCELAR_LLAMADA:
        this.videoCallingVisible = false;
        this.nombreContacto = undefined;
        this.videollamada = undefined;
      break;
      // --- Timeout videollamada ---
      case TipoMensaje.TIMEOUT_LLAMADA:
        this.videoCallingVisible = false;
        this.nombreContacto = undefined;
        this.videollamada = undefined;
        alert(mensaje.contenido.mensaje);
      break;
      // --- Videollamada Terminada ---
      case TipoMensaje.TERMINAR_VIDEOLLAMADA:
        this.videollamadaService.disconnectVideollamadaSession();
        alert(mensaje.contenido.mensaje);
        this.router.navigate(['']);
      break;
      // --- Actualizacion contactos ---
      case TipoMensaje.ACTUALIZAR_CONTACTOS:
        const subscribeContactos$ = this.contactoAgenteService.buscarTodosContactoAgente()
        .pipe(
          map(contactos => {
             return contactos.filter( c => c.id !== this.usuarioApp.id);
          })
        ).subscribe(contactos => {
          this.contactos = contactos;
          subscribeContactos$.unsubscribe();
        });
        this.notificacionService.infoAlert(mensaje.contenido.mensaje);
      break;
    }
  }

  @HostListener('window:beforeunload')
  windowBeforeUnload(): void {
    this.subscripcionMensajesWs.unsubscribe();
  }

}
