import { Injectable } from '@angular/core';
import { ContactoAgente } from '../domain/cckall.domain';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import * as api from '../config/config';

@Injectable({
  providedIn: 'root'
})
export class ContactoAgenteService {

  private endpoint: string;

  constructor(
    private http: HttpClient
  ) {
    this.endpoint = api.WEBSERVICE_VIDEOLLAMADAS;
   }

  buscarContactoAgenteApp(id: Number): Observable<ContactoAgente> {
    return this.http.get<ContactoAgente>(this.endpoint + '/login/cckall/' + id);
  }

  buscarTodosContactoAgente(): Observable<ContactoAgente[]> {
    return this.http.get<ContactoAgente[]>(this.endpoint + '/agente');
  }

}
