import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  private localStorage;

  constructor() {
    this.localStorage = localStorage;
   }

   guardarValor(key: string, value: any) {
    if (key === null ) {
      throw new Error('key a guardar esta en nulo!!!');
    }
    if (value === null ) {
      throw new Error('valor a guardar esta en nulo!!!');
    }
    this.localStorage.setItem(key, JSON.stringify(value));
  }

  obtenerValor(key: string) {
    const valor = this.localStorage.getItem(key);
    if (valor !== null) {
      return JSON.parse(valor);
    } else {
      return null;
    }
  }

  eliminarValor(key: string) {
    this.localStorage.removeItem(key);
  }

}
