import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class NotificacionService {

  /**
   * Encargado de escuchar las peticiones de mensajes.
   **/
  subjectNotificacion$: Subject<Notificacion> = new Subject();

  constructor() { }

  getNotificaciones(): Observable<Notificacion> {
    return this.subjectNotificacion$.asObservable();
  }

  private show(tipo, nivel, titulo, msg) {
    const notificacion = {
      titulo: titulo,
      mensaje: msg,
      nivel: nivel,
      visible: true,
      tipo: tipo
    };
    this.subjectNotificacion$.next(notificacion);
  }

  successAlert(mensaje: string) {
    this.show(Tipo.ALERT, 'success', 'Exito', mensaje);
  }

  infoAlert(mensaje: string) {
    this.show(Tipo.ALERT, 'info', 'Información', mensaje);
  }

  warningAlert(mensaje: string) {
    this.show(Tipo.ALERT, 'warning', 'Advertencia', mensaje);
  }

  errorAlert(mensaje: string) {
    this.show(Tipo.ALERT, 'error', 'Error', mensaje);
  }

  successNotify(mensaje: string) {
    this.show(Tipo.NOTIFY, 'success', 'Exito', mensaje);
  }

  infoNotify(mensaje: string) {
    this.show(Tipo.NOTIFY, 'info', 'Información', mensaje);
  }

  warningNotify(mensaje: string) {
    this.show(Tipo.NOTIFY, 'warning', 'Advertencia', mensaje);
  }

  errorNotify(mensaje: string) {
    this.show(Tipo.NOTIFY, 'error', 'Error', mensaje);
  }

}

export interface Notificacion {
  titulo: string;
  mensaje: string;
  visible: boolean;
  nivel: Nivel;
  tipo: Tipo;
}

export enum Nivel {
  SUCCESS = 'success',
  INFO = 'info',
  WARN = 'warn',
  ERROR = 'error'
}

export enum Tipo {
  ALERT = 'ALERT',
  NOTIFY = 'NOTIFY'
}
