import { Component, OnInit, OnDestroy } from '@angular/core';
import { VideoLlamadaService } from 'src/app/service/video-llamada.service';
import { Subscription } from 'rxjs';
import { StreamEvent, StreamManager, Subscriber, Publisher } from 'openvidu-browser';
import { WebsocketService } from 'src/app/service/websocket.service';
import { TipoMensaje } from 'src/app/domain/websocket.domain';
import { SessionService } from 'src/app/service/session.service';

@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.css']
})
export class VideoComponent implements OnInit, OnDestroy {

  // --- Objetos openvidu ---
  publisher: StreamManager; // Local
  subscribers: StreamManager[] = []; // Remotes
  mainStreamManager: StreamManager; // updated by click event in UserVideoComponent children
  // --- Subscripciones a servicios de videollamada ---
  subscripcionConexion: Subscription;
  subscripcionStreamCreated: Subscription;
  subscripcionStreamDestroyed: Subscription;
  // --- Propiedades de Componente ---
  tituloSesion = 'Ejemplo video';
  sesionEstablecida = false;

  constructor(
    private videollamadaService: VideoLlamadaService,
    private websocketService: WebsocketService,
    private sesionService: SessionService
  ) {}

  ngOnInit() {
    console.log('componente video INICIADO!!!');
    // ---Propiedades para crear conexion openvidu ---
    const token = this.sesionService.obtenerSesionVideoLLamada().token;
    const usuario = this.sesionService.obtenerUsuarioApp().usuarioOperkall;
    console.log('usuario:' + usuario + ', token:' + token);
    // --- Crear las propiedades del publicador ---
    const propPublisher = {
      audioSource: undefined, // The source of audio. If undefined default microphone
      videoSource: undefined, // The source of video. If undefined default webcam
      publishAudio: true,     // Whether you want to start publishing with your audio unmuted or not
      publishVideo: true,     // Whether you want to start publishing with your video enabled or not
      resolution: '640x480',  // The resolution of your video
      frameRate: 30,          // The frame rate of your video
      insertMode: 'APPEND',   // How the video is inserted in the target element 'video-container'
      mirror: false           // Whether to mirror your local video or not
    };
    this.subscripcionConexion = this.videollamadaService
    .connectVideollamadas(token, usuario, propPublisher)
    .subscribe( (publisher: Publisher) => {
      this.publisher = publisher;
      this.sesionEstablecida = true;
      console.log('Conexion servidor videollamadas establecida ok!!!');
    });
    this.subscripcionStreamCreated = this.videollamadaService.getStreamCreated()
    .subscribe(
      (event: StreamEvent) => {
        console.log('stream creado!!!');
        // Subscribe to the Stream to receive it. Second parameter is undefined
        // so OpenVidu doesn't create an HTML video by its own
        // this.session.subscribe(event.stream, undefined);
        const subscriber = this.videollamadaService.subscribeSession(event.stream, undefined);
        this.subscribers.push(subscriber);
      }
    );
    this.subscripcionStreamDestroyed = this.videollamadaService.getStreamDestroyed()
    .subscribe(
      (event: StreamEvent) => {
        console.log('stream destruido!!!');
        this.videollamadaService.deleteSubscriber(event.stream.streamManager);
      }
    );

  }

  ngOnDestroy(): void {
    console.log('se unsubscriben algunos servicios');
   this.subscripcionConexion.unsubscribe();
   this.subscripcionStreamCreated.unsubscribe();
   this.subscripcionStreamDestroyed.unsubscribe();
  }

  updateMainStreamManager(streamManager: StreamManager) {
    this.mainStreamManager = streamManager;
  }

  disconnectVideollamadaSession(): void {
    this.sesionEstablecida = false;
    // this.videollamadaService.disconnectVideollamadaSession();
    // --- SE ENVIARA MENSAJE PARA TERMINAR VIDEOLLAMADA ---
    const videollamadaId = this.sesionService.obtenerSesionVideoLLamada().videollamadaId;
    console.log('Peticion desconeccion sesion openvidu videollamada [id=' + videollamadaId + ']');
    const contenido = {
      tipoMensaje: TipoMensaje.TERMINAR_VIDEOLLAMADA,
      contenido:  {
        mensaje: videollamadaId
      }
    };
    this.websocketService.enviarContenidoMensajeWebsocket(contenido);
  }
}
