import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { MensajeVideoLLamada, TipoMensaje, MensajeWebsocket } from 'src/app/domain/websocket.domain';
import { WebsocketService } from 'src/app/service/websocket.service';

@Component({
  selector: 'app-video-calling',
  templateUrl: './video-calling.component.html',
  styleUrls: ['./video-calling.component.css']
})
export class VideoCallingComponent implements OnInit {

  @Input()
  visible: boolean;
  @Input()
  nombreContacto: string;
  @Input()
  videollamada: MensajeVideoLLamada;
  @Input()
  saliente: boolean;
  @Output()
  cancelarLLamada = new EventEmitter<MensajeVideoLLamada>();
  @Output()
  contestarLLamada = new EventEmitter<MensajeVideoLLamada>();

  constructor(
    private websocketService: WebsocketService
  ) { }

  ngOnInit() {
    // this.visible = true;
  }

  onCancelarLLamada(mensajeVideollamada: MensajeVideoLLamada): void {
    const contenido = {
      tipoMensaje: TipoMensaje.SOLICITUD_CANCELAR_LLAMADA,
      contenido:  {
        videollamadaId: mensajeVideollamada.videollamadaId,
        notificarContactos: [
          mensajeVideollamada.emisor,
          mensajeVideollamada.receptor
        ]
      }
    };
    this.websocketService.enviarContenidoMensajeWebsocket(contenido);
    // this.cancelarLLamada.emit(mensajeVideollamada);
  }

  onContestarLLamada(mensajeVideollamada: MensajeVideoLLamada): void {
    const req = new MensajeWebsocket();
    req.contenido = {
      videollamadaId : mensajeVideollamada.videollamadaId,
      emisor : mensajeVideollamada.emisor,
      receptor: mensajeVideollamada.receptor
    };
    req.fecha = new Date();
    req.tipoMensaje = TipoMensaje.CONTESTAR_LLAMADA;
    this.websocketService.enviarMensajeWebsocket(req);
    // this.contestarLLamada.emit(mensajeVideollamada);
  }
}
