import { Component, Input, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { StreamManager } from 'openvidu-browser';

@Component({
  selector: 'app-user-video',
  template: `
        <div class="contenedor-video-user">
            <video #videoElement [style.width.px]="anchoVideo" [style.heigth.px]="altoVideo"
             poster="/assets/img/webrtc.png" ></video>
            <div class="titulo-video background-transparente"><p>{{ nombreContacto }}</p></div>
        </div>`,
  styleUrls: ['./user-video.component.css']
})
export class UserVideoComponent  implements AfterViewInit {

    // --- propiedades de componente ---
    @Input()
    public nombreContacto: string;
    @Input()
    public anchoVideo: string;
    @Input()
    public altoVideo: string;
    // --- VIDEO STREAMING ---
    _streamManager: StreamManager;
    @ViewChild('videoElement') elementRef: ElementRef;

    ngAfterViewInit() {
        this._streamManager.addVideoElement(this.elementRef.nativeElement);
    }

    /**
     * Metodo actua como input de componente.
     */
    @Input()
    set streamManager(streamManager: StreamManager) {
        this._streamManager = streamManager;
        if (!!this.elementRef) {
            this._streamManager.addVideoElement(this.elementRef.nativeElement);
        }
    }

}
