import { Component, OnInit, OnDestroy } from '@angular/core';
import { NotificacionService, Notificacion } from 'src/app/service/notificacion.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-notificacion',
  templateUrl: './notificacion.component.html',
  styleUrls: ['./notificacion.component.css']
})
export class NotificacionComponent implements OnInit, OnDestroy {

  timeoutMsg = 3000;
  notificaciones: Notificacion[] = [];
  suscripcionNotificaciones: Subscription;

  constructor(
    private notificacionService: NotificacionService
  ) { }

  ngOnInit() {
    this.suscripcionNotificaciones = this.notificacionService.subjectNotificacion$
    .subscribe(
      notificacion => {
        console.log('Enviando mensaje ' + notificacion.nivel + ' - ' + notificacion.mensaje);
        this.notificaciones.push(notificacion);
        setTimeout(() => {
          this.eliminarNotificacion(notificacion);
        }, this.timeoutMsg);
    });
  }

  ngOnDestroy() {
    this.suscripcionNotificaciones.unsubscribe();
  }

  eliminarNotificacion(notificacion: Notificacion) {
    this.notificaciones = this.notificaciones.filter(x => x !== notificacion);
  }

}
